<template>
  <div class="wrapper">
    <div class="container">
      <h1>
        出差申请
      </h1>
      <hr/>
      <form target="targetIfr">
        <table>
          <tr>
            <td>
              申请人：&emsp;<span>{{userData.userName}}</span>
            </td>
            <td>
               员工编号：<span>{{userData.userId}}</span>
            </td>
            <td>
               就职部门：<span>{{userData.userDepartment}}</span>
            </td>
          </tr>
          <tr>
            <td>
               出差地点：<input type="text" id="site" name="site">
            </td>
            <td>
                开始时间：<input name="start" id="startTime" type="date" value="2018-01-01"/>
            </td>
            <td>
                结束时间：<input name="end" id="endTime" type="date" value="2018-12-01"/>
            </td>
          </tr>
          <tr>
            <td colspan="3">
              出差事由：
              <textarea  name="reason" id="reason"></textarea>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <input type="radio" name="isExigency" id="isExigency" value="紧急">紧急事务，望上级尽快批准
            </td>
            <td>
              <input type="submit" value="提交" @click="businessSubmit">
            </td>
          </tr>
        </table>
      </form>
      <iframe name="targetIfr" style="display:none"></iframe> 
    </div>
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
import axios from 'axios'
export default {
  name: "businessTrip",
  data(){
    return {
      userData:{},
    }
  },
  mounted:function(){
    this.userData = JSON.parse(localStorage.getItem("information")).data;
  },
  methods:{
  businessSubmit:function(){
     var userId = this.userData.userId;
      var startTime = document.getElementById("startTime").value;
      var endTime = document.getElementById("endTime").value;
      var reason = document.getElementById("reason").value;
      var site = document.getElementById("site").value;
      var isExigency = document.getElementById("isExigency");
      if(isExigency.checked){
        isExigency = 1;
      }else{
        isExigency = 0;
      }
      axios({
        method:"get",
        url:urlHeader+"/leave/addLeave?userId="+
          userId+
          "&start="+
          startTime+
          "&end="+
          endTime+
          "&reason="+
          reason+
          "&site="+
          site+
          "&isExigency="+
          isExigency+
          "&type="+"2"
          }).then((Response)=>{
            if(Response.data.code){
              alert("申请成功")
            }else{
              alert("申请失败")
            }
          }).catch((err)=>{
            console.log(err);
          })
       }
    }
  }
</script>
<style lang="less" scoped>
.wrapper {
  margin-left: 8%;
  .container {
    width: 900px;
    height: 100%;
    margin: 0 auto;
    h1 {
      font-family: "宋体";
      font-size: 20px;
      line-height: 40px;
    }
    hr {
      width: 100%;
      height: 1px;
      border: none;
      background-color: #cccccc;
    }

    form {
      width: 800px;
      margin: 0 auto;
      margin-top: 50px;
      text-align: center;
      table {
        font-family: "仿宋";
        font-size: 13px;
        input[type="text"] {
          width: 120px;
          height: 20px;
          padding-left: 5px;
        }
        td {
          width: 220px;
          height: 50px;
          span {
            display: inline-block;
            width: 120px;
            text-align: left;
            padding-left: 5px;
          }
        }
        textarea {
          width: 570px;
          height: 175px;
          resize: none;
          font-size: 15px;
          padding: 5px;
        }
        input[type="submit"] {
          font-family: "仿宋";
          font-size: 13px;
          background-color: #3b4966;
          color: #ffffff;
          width: 50px;
          letter-spacing: 2px;
          height: 30px;
          border-radius: 3px; // outline: none;
          border: none;
        }
      }
    }
  }
}
</style>



