<template>
  <div class="wrapper">
      <div class="container">
          <p>出差审核</p>
          <hr>
          <div class="loading">{{loading}}</div>
          <table>
            <tr v-for="(item,index) in leaveList" :key="index" @click="ShowInformation(index)" v-if="start<=index&&index<end">
              <a href="javascript:void(0)">
              <td>
                  <span v-if="item.leave.isExigency">【紧急】</span>
                  <span v-else></span>   
                  {{item.leave.userId}}-{{item.userName}}  {{item.leave.date}}日   {{item.leave.site}}出差申请
                </td>
              </a>
            </tr>     
          </table>
          <div class="btn">
            <a href="javascript:void(0)" class="last" @click="lastPage">上一页</a>
            <span class="page">{{now}}/{{all}}页</span>
            <a href="javascript:void(0)" class="next" @click="nextPage">下一页</a>
          </div>
      </div>
      <check-information class="checkInformation" v-show="checkInformationShow" :message="checkMessage" v-on:listenChange="listenChange"></check-information>
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
import axios from 'axios'
import checkInformation from './checkInformation'
import leaveVue from './leave.vue';
export default {
  name: "businessCheck",
  data(){
    return {
      leaveList:[],
      checkInformationShow:false,
      checkMessage:{
        "auditorName": "admin",
        "leave": {
          "auditorId": 2,
          "date": "2018-4-16",
          "end": "2018-6-01",
          "id": 1,
          "isExigency": 1,
          "reason": "生病",
          "rejection": "我拒绝",
          "site": null,
          "start": "2018-5-01",
          "status": 2,
          "type": 1,
          "userId": 4
        },
      "userName": "tanwt"
      },
      now:1,
      all:0,
      start:0,
      end:10,
      loading:"正在获取数据..."
    }
  },
  components:{
    checkInformation
  },
  mounted:function(){
    var that = this;
    var userInformation = JSON.parse(localStorage.getItem("information")).data;
    var userId = userInformation.userId;
    var department = userInformation.userDepartment;
    axios({
      method:"post",
       url:urlHeader+"/leave/getOtherLeaveById?userId="+userId,
    }).then((Response)=>{
      Response.data.data.forEach((item,index,array) => {
        if(item.leave.type==2&&item.leave.status==0){
          item.department = department;
          that.leaveList.push(item);
          that.all = Math.ceil(that.leaveList.length/10);
        }
      });
      this.loading="";
    }).catch((err)=>{
      console.log(err)
    })
  },
  methods:{
    ShowInformation:function(n){
      this.checkMessage = this.leaveList[n];
      this.checkInformationShow = !this.checkInformationShow;
    },
    listenChange:function(data){
      this.checkInformationShow = data;
    },
    lastPage: function(){
      if(this.start<=0){
        alert("已经是第一页了");
      }else{
        this.start-=10;
        this.end-=10;
        this.now--;
      }
    },
    nextPage: function(){
      if(this.end>this.data.length){
        alert("已经是最后一页了");
      }else{
        this.start+=10;
        this.end+=10;
        this.now++;
      }
    },
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  margin-left: 8%;
  a{
    color:inherit;
    text-decoration: none;
  }
  .container {
    width: 900px;
    margin: 0 auto;
    p {
      font-family: "宋体";
      font-size: 20px;
      line-height: 40px;
    }
    hr {
      width: 100%;
      height: 1px;
      border: none;
      background-color: #cccccc;
    }
    .loading{
      width:170px;
      margin:0 auto;
      margin-top: 5px;
    }
    table {
      width: 700px;
      margin: 0 auto;
      margin-top: 20px;
      border: 1px solid #cccccc;
      font-family: "宋体";
      border-collapse: collapse;
      text-align: left;
      tr:nth-child(even) {
        background-color: #f9f9f9;
      }
      td {
        width: 700px;
        height: 30px;
        line-height: 30px;
      }
    }
    .btn {
      width: 700px;
      height: 30px;
      margin: 0 auto;
      display: flex;
      justify-content: space-around;
      a {
        border: none;
        background: none;
        font-family: "仿宋";
        margin-top: 20px;
        color: inherit;
        text-decoration: none;
      }
      .page{
        font-family: "仿宋";
        margin-top: 20px;
      }
    }
  }
  .checkInformation{
       width: 700px;
       margin: 0 auto;
       position: absolute;
       top:15%;
       left:27%;
    }
}
</style>



