<template>
  <div class="wrapper">
      <div class="container">
        <img src="../assets/images/userPhoto.png">
        <p class="name">{{information.userName}}</p>
        <table v-show="isShow">
            <tbody>
            <tr>
                <td>员工编号:</td>
                <td>{{information.userId}}</td>
            </tr>
            <tr>
                <td>部门：</td>
                <td>{{information.userDepartment}}</td>
            </tr>
            <tr>
                <td>性别:</td>
                <td v-if="information.userSex=='M'">男</td>
                <td v-else>女</td>
            </tr>
             <tr>
                <td>婚姻状况:</td>
                <td v-if="information.userMarriage">已婚</td>
                <td v-else>未婚</td>
            </tr>
            <tr>
                <td>联系电话:</td>
                <td>{{information.userPhone}}</td>
            </tr>
            <tr>
                <td>电子邮箱:</td>
                <td>{{information.userMail}}</td>
            </tr>
            </tbody>
        </table>
        <table v-show="!isShow">  
            <tbody>
                <form>
            <tr>
                <td>员工编号:</td>
                <td><input type="text" id="userId" name="userId" :value="information.userId" disabled></td>
            </tr>
            <tr>
                <td>部门：</td>
                <td>
                  <select name="userDepartment" id="userDepartment">
                    <option>人力资源部</option>
                    <option>财务部</option>
                    <option>生产技术部</option>
                    <option>营销部</option>
                    <option>行政部</option>
                  </select>
                </td>
            </tr>
            <tr>
                <td>性别:</td>
                <td><input type="radio" name="userSex" v-if="information.userSex=='M'" value="M" checked>
                <input type="radio" name="userSex" value="M" v-else>
                男
                    &emsp;&emsp;&emsp;<input type="radio" name="userSex" value="W" v-if="information.userSex=='W'" checked>
                    <input type="radio" name="userSex" value="W" v-else>
                  女</td>
            </tr>
             <tr>
                <td>婚姻状况:</td>
                <td><input type="radio" name="userMarriage" value="1"  v-if="information.userMarriage==1" checked>
                    <input type="radio" name="userMarriage" value="1"  v-else>已婚
                    &emsp;&emsp;<input type="radio" name="userMarriage" value="0" v-if="information.userMarriage==0" checked>
                    <input type="radio" name="userMarriage" value="0"  v-else>未婚
                </td>
            </tr>
            <tr>
                <td>联系电话:</td>
                <td><input type="text"  name="userPhone" id="userPhone" :value="information.userPhone"></td>
            </tr>
            <tr>
                <td>电子邮箱:</td>
                <td><input type="text" id="userMail" name="userMail" :value="information.userMail"></td>
            </tr>
            </form>
            </tbody>
        </table>
        <button @click="isChange" v-show="isShow">
            修改资料
        </button>
        <button @click="isChange" v-show="!isShow">
            保&emsp;&emsp;存
        </button>
      </div>    
  </div>
</template>
<script>
var sex,marriage;
var userInformation={};
var urlHeader="http://120.78.187.54:8080/text";
import axios from "axios";
export default {
  name: "user",
  data() {
    return {
      isShow: true,
      word: "修改资料",
      information: ""
    };
  },
  methods: {
    isChange: function() {
      this.isShow = !this.isShow;
      if (this.isShow){
        console.log(this.isShow);
        var userId = document.getElementById("userId").value;
        var userSex = document.getElementsByName("userSex");
        var userMarriage = document.getElementsByName("userMarriage");
        var userDepartment = document.getElementById("userDepartment").value;
        var userPhone = document.getElementById("userPhone").value;
        var userMail = document.getElementById("userMail").value;
        var userName = this.information.userName;
        var userPassWord = this.information.userPassWord;
        var userState = this.information.userState; 
        var xhr = new XMLHttpRequest();
        for(var i=0;i<2;i++){
        if(userSex[i].checked){
          sex = userSex[i].value;
        } 
      }
      for(var i=0;i<2;i++){
        if(userMarriage[i].checked){
          marriage = userMarriage[i].value;
        }
      }
        xhr.open(
          "get",
            urlHeader+"/user/userUpdate?userId=" +
            userId +
            "&userSex=" +
            sex+
            "&userDepartment="+
            userDepartment+
            "&userMarriage="+
            marriage+
            "&userPhone="+
            userPhone+
            "&userMail="+
            userMail+
            "&userName="+
            userName+
            "&userPassWord="+
            userPassWord+
            "&userState="+
            userState,
          true
        );
        xhr.send(null);
        var _this=this;
        xhr.onreadystatechange = function() {
          if (xhr.readyState == 4) {
            if ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304) {
              userInformation = JSON.parse(xhr.responseText);
              if(userInformation.code==1){
                alert("保存成功");
                _this.information.userSex = sex;
                _this.information.userMarriage = marriage;
                _this.information.Department =userDepartment;
                _this.information.userPhone = userPhone;
                _this.information.userMail = userMail;
              }else{
                alert("保存失败")
              }
              }
            }
          }
        };
      }
    },
  mounted: function() {
    var userInformation = JSON.parse(localStorage.getItem("information"));
    this.information = userInformation.data;
  }
};
</script>
<style lang="less" scoped>
.wrapper {
  margin-left: 8%;
  .container {
    width: 400px;
    height: 100%;
    margin: 0 auto;
    margin-top: 50px;
    img {
      width: 100px;
      height: 100px;
      margin-left: 130px;
      border-radius: 50%;
    }
    .name {
      margin-left: 150px;
      margin-top: 10px;
      font-family: "宋体";
    }
    button {
      padding: 2px 10px;
      margin-left: 140px;
      margin-top: 20px;
    }
  }
  table {
    margin-top: 30px;
    width: 400px;
    border-collapse: collapse;
    tr {
      height: 40px;
    }
    tr:nth-child(odd) {
      background-color: #f9f9f9;
    }
    td {
      padding-left: 20px;
      font-size: 13px;
      font-family: "仿宋";
      font-weight: bolder;
      height: 30px;
      line-height: 40px;
      span{
        font-size: 8px;
        font-family: "微软雅黑"
      }
      input[type="text"] {
        width: 250px;
        margin-right: 40px;
        padding: 2px;
      }
    }
  }
  button {
    background-color: #3b4966;
    color: #ffffff;
    padding: 5px;
    letter-spacing: 2px;
    height: 30px;
    border-radius: 3px; // outline: none;
    border: none;
  }
}
</style>



