<template>
  <div class="wrapper">
    <div class="container">
      <h1>
        请假申请
      </h1>
      <hr/>
      <form target="targetIfr">
        <table>
          <tr>
            <td>
              申请人：&emsp;<span>{{userData.userName}}</span>
            </td>
            <td>
               员工编号：<span>{{userData.userId}}</span>
            </td>
            <td>
               就职部门：<span>{{userData.userDepartment}}</span>
            </td>
          </tr>
          <tr>
            <td>
                开始时间：<input id="startTime" name="start" type="date" value="2018-01-01"/>
            </td>
            <td>
                结束时间：<input id="endTime" name="end" type="date" value="2018-12-01"/>
            </td>
            <td>
               <input type="radio" name="type" value="1">事假
               &emsp;&emsp;
               <input type="radio" name="type" value="0">病假
            </td>
          </tr>
          <tr>
            <td colspan="3">
              请假事由：
              <textarea id="reason" name="reason"></textarea>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <input type="radio" id="isExigency" name="isExigency" value="1">紧急事务，望上级尽快批准
            </td>
            <td>
              <button @click="leaveSubmit">提交</button>
            </td>
          </tr>
        </table>
      </form>
      <iframe name="targetIfr" style="display:none"></iframe> 
    </div>
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
import axios from 'axios'
export default {
  name: "leave",
  data(){
    return {
      userData:{}
    }
  },
  mounted:function(){
    this.userData = JSON.parse(localStorage.getItem("information")).data;
    // console.log(userData);
  },
  methods:{
    leaveSubmit:function(){
      var type;
      var userId = this.userData.userId;
      var startTime = document.getElementById("startTime").value;
      var endTime = document.getElementById("endTime").value;
      var reason = document.getElementById("reason").value;
      var radio = document.getElementsByName("type");
      console.log(radio);
      var isExigency = document.getElementById("isExigency");
      console.log(isExigency);
      for(var i=0;i<2;i++){
        if(radio[i].checked){
          type = i
        }
      }
      if(isExigency.checked){
        isExigency = 1;
      }else{
        isExigency = 0;
      }
      axios({
        method:"get",
        url:urlHeader+"/leave/addLeave?userId="+
      userId+
      "&start="+
      startTime+
      "&end="+
      endTime+
      "&reason="+
      reason+
      "&type="+
      type+
      "&isExigency="+
      isExigency
      }).then((Resopnse)=>{
        if(Resopnse.data.code==1){
          alert("申请成功");
        }else{
          alert("申请失败")
        }
      }).catch((err)=>{
        console.log(err);
      })
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper{
  margin-left:8%;
.container{
  width: 900px;
  height:100%;
  margin: 0 auto;
  h1{
    font-family: "宋体";
    font-size: 20px;
    line-height: 40px;
  }
  hr {
    width: 100%;
    height: 1px;
    border: none;
    background-color: #cccccc;
  }
  
  form{
      width: 800px;
      margin:0 auto;
      margin-top: 50px;
      text-align: center;
    table{
      font-family: "仿宋";
      font-size: 13px;
      input[type="text"]{
        width: 120px;
        height:20px;
        padding-left: 5px;
      }
      td{
        width: 220px;
        height:50px;
        span{
          display: inline-block;
          width: 120px;
          text-align: left;
          padding-left: 5px;
        }
      }
      textarea{
        width:570px;
        height:175px;
        resize: none;
        font-size:15px;
        padding:5px;
      }
      button{
        font-family: "仿宋";
        font-size: 13px;
        background-color: #3B4966;
        color: #ffffff;
        width: 50px;
        letter-spacing: 2px;
        height:30px;
        border-radius:3px;    // outline: none;
        border:none;
      }
    }
  }
}
}
</style>



