<template>
  <div id="app">
   <div class="header">
      <p>
        考勤管理系统
        <a href="javascript:void(0)" @click="turnBack">
          {{message}}
        </a>
      </p>   
    </div> 
    <usercommon v-show="isShow" :message="stateMessage"></usercommon>
    <router-view class="other"></router-view>
  </div>
</template>
<script>
import usercommon from './components/common'
export default {
  name:"App",
  components:{
    usercommon,
  },
  data(){
    return{
      message:"退出登录",
      isShow:true,
      stateMessage:0
    }
  },
  methods:{
    turnBack:function(){ 
      this.$router.push("/enter");
      localStorage.removeItem("information");  
    }
  },
  watch:{
    $route(to,from){
      if(to.path=="/enter"){
        this.message="";
        this.isShow = false;
      }else{
        this.message="退出登录";
        this.isShow = true;
        var userState = JSON.parse(localStorage.getItem("information")).data.userState;
        this.stateMessage = userState;
      }
    }
  },
  mounted:function(){
    if(this.$route.path=="/enter"){
      this.message=""
    }
      var information=JSON.parse(localStorage.getItem("information"));
      if(information&&this.$route.path!="/enter"){
        this.$router.push("/user");
      }else{
        this.$router.push("/enter");
      }
      
    },
  destroyed:function(){
    localStorage.clear();
  }   
}
</script>
<style lang="less" scoped>
#app{
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  overflow-x: hidden;
}
.header{
  width: 100%;
  height: 8%;
  background-color: #205AA4;
  display: block;
  position: relative;
  z-index: 2;
  p{
    line-height: 50px;
    font-size:22px;
    color:#F2FFF9;
    font-weight: bolder;
    padding-left: 30px;
    letter-spacing: 2px;
    a{
      font-size: 12px;
      font-weight: normal;
      font-family: "宋体";
      position: absolute;
      right:20px;
      color:inherit;
    }
  }
}
 .other{
    height:80%;
  }
</style>


