<template>
<div class="wrapper">
     <div class="container">
        <p>全部打卡情况</p>
        <hr>
        <div class="loading">{{loading}}</div>
        <table>
          <tr>
            <th>员工编号</th>
            <th>日期</th>
            <th>姓名</th>
            <th>上班时间</th>
            <th>下班时间</th>
            <th>打卡情况</th>
          </tr>
           <tr v-for="(item,index) in data" :key="index" v-if="start<=index&&index<=end">
            <td>{{item.clockCard.userId}}</td>
            <td>{{item.clockCard.date}}</td>
            <td>{{item.userName}}</td>
            <td>{{item.clockCard.startTime}}</td>
            <td>{{item.clockCard.endTime}}</td>
            <td :style="{color:item.color}">{{item.message}}</td>
          </tr>
        </table>
        <div class="btn">
          <a href="javascript:void(0)" class="last" @click="lastPage">上一页</a>
          <span class="page">{{now}}/{{all}}页</span>
          <a href="javascript:void(0)" class="next" @click="nextPage">下一页</a>
        </div>
      </div>
</div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
import axios from 'axios';
export default {
  name:"seeAll",
  data(){
    return{
      data:[],
      start:0,
      end:10,
      now:1,
      all:0,
      loading:"正在获取数据..."
    }
  },
  mounted:function(){
    var userInformation = JSON.parse(localStorage.getItem("information")).data;
    var userId = userInformation.userId;
    axios({
      methods:"get",
      url:urlHeader+"/clockCard/getOtherClockById?userId="+userId
    }).then((Response)=>{
      this.data = Response.data.data;
      this.all = Math.ceil(this.data.length/10);
      this.data.forEach(function(item,index,array){
        if(item.clockCard.status==1){
         item.color="#FF39FF"
         item.message = "迟到";
        }else if(item.clockCard.status==2){
          item.color="#D73636"
          item.message = "旷工";
        }else if(item.clockCard.status==0){
          item.color="#408C40"
          item.message = "出勤";
        }else{
          item.color="#D73636"
          item.message = "旷工 迟到";
        }
      })
      this.loading = "";
    }).catch((err)=>{
      console.log(err);
    })
  },
  methods:{
    lastPage: function(){
      if(this.start<=0){
        alert("已经是第一页了");
      }else{
        this.start-=10;
        this.end-=10;
        this.now--;
      }
    },
    nextPage: function(){
      if(this.end>=this.data.length){
        alert("已经是最后一页了");
      }else{
        this.start+=10;
        this.end+=10;  
        this.now++; 
      }
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper{
  margin-left:8%;
.container {
  width: 900px;
  margin: 0 auto;
    p{
        font-family: "宋体";
        font-size: 20px;
        line-height: 40px;
  }
    hr {
      width: 100%;
      height: 1px;
      border: none;
      background-color: #cccccc;
    }
    .loading{
      width:170px;
      margin:0 auto;
      margin-top: 5px;
    }
    table {
      width: 700px;
      margin:0 auto;
      margin-top:20px;
      border: 1px solid #cccccc;
      font-family: "宋体";
      border-collapse: collapse;
      text-align: center;
      tr:nth-child(even){
        background-color: #F9F9F9;
      }
      th,td {
        width: 150px;
        height: 30px;
        line-height: 30px;
      }
    }
    .btn{
      width: 700px;
      height:30px;
      margin:0 auto;
      display: flex;
      justify-content:space-around;
      a{
        border:none;
        background:none;
        font-family: "仿宋";
        margin-top: 20px;
        color:inherit;
        text-decoration: none;
      }
       .page{
        font-family: "仿宋";
        margin-top: 20px;
      }
    }
  }
}
</style>



