<template>
  <div class="wrapper">
    <div class="container">
      <p>今天是：{{nowTime}}</p>
      <hr/>
      <table class="signin">
        <tr>
          <td>
            <button @click="worksignIn(0,$event)">上班打卡</button>
          </td>
          <td>
            {{workTime[0]}}
          </td>
          <td>
          </td>
          <td>
            <button @click="worksignIn(1,$event)">下班打卡</button>
          </td>
          <td>
            {{workTime[1]}}
          </td>
        </tr>
      </table>
      <hr/>
      <div class="historySignIn">
        <p>历史打卡信息</p>
        <table>
          <tr>
            <th>日期</th>
            <th>上班时间</th>
            <th>下班时间</th>
            <th>出勤情况</th>
          </tr>
          <tr v-for="(item,index) in data" :key="index" v-if="start<=index&&index<end">
            <td>{{item.date}}</td>
            <td>{{item.startTime}}</td>
            <td>{{item.endTime}}</td>
            <td :style="{color:item.color}">{{item.message}}</td>
          </tr>
        </table>
        <div class="btn">
          <a href="javascript:void(0)" class="last" @click="lastPage">上一页</a>
          <span class="page">{{now}}/{{all}}页</span>
          <a href="javascript:void(0)" class="next" @click="nextPage">下一页</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
var userId;
var date = new Date();
var year = date.getFullYear();
var month = date.getMonth() + 1;
var day = date.getDate();
var a = new Array("日", "一", "二", "三", "四", "五", "六");
var week = new Date().getDay();
var today = year + "-" + month + "-" + day + "   星期" + a[week];
function toTimeString(someDate) {
  var dateHour = someDate.getHours();
  var dateMinutes = someDate.getMinutes();
  return dateHour + ":" + dateMinutes;
}

import axios from "axios";
export default {
  name: "signIn",
  data() {
    return {
      nowTime: today,
      workTime: [],
      data:[],
      start:0,
      end:8,
      now:1,
      all:10,
    }
  },
  methods: {
    worksignIn: function(index, event) {
      var wdate = new Date();
      this.workTime.splice(index, 1, toTimeString(wdate));
      var target = event.target;
      var url;
      if(index==0){
        url = urlHeader+"/clockCard/startWork?userId="+userId;
        console.log(url);
      }else{
        url = urlHeader+"/clockCard/endWork?userId="+userId;
      }
      var xhr = new XMLHttpRequest();
      xhr.open("get",url,true);
      xhr.send(null);
       xhr.onreadystatechange=function(){
        if(xhr.readyState==4){
          if((xhr.status>=200||xhr.status<300)&&xhr.status==304){
            console.log(xhr.responseText);
            console.log(1);
          }
        }
      }
    },
    lastPage: function(){
      if(this.start<=0){
        alert("已经是第一页了");
      }else{
        this.start-=8;
        this.end-=8;
        this.now--;
      }
    },
    nextPage: function(){
      if(this.end>this.data.length){
        alert("已经是最后一页了");
      }else{
        this.start+=8;
        this.end+=8;
        this.now++;
      }
    },
  },
  mounted:function(){
    userId = JSON.parse(localStorage.getItem("information")).data.userId;
    axios({
      method:'get',
      url:urlHeader+'/clockCard/getAllClockById?userId='+userId,
    }).then((Response)=>{
      this.data = Response.data.data;
      this.all = Math.ceil(this.data.length/8);
      console.log(this.data);
      this.data.forEach(function(item,index,array){
        if(item.status==1){
         item.color="#FF39FF"
         item.message = "迟到";
        }else if(item.status==2){
          item.color="#D73636"
          item.message = "旷工";
        }else if(item.status==0){
          item.color="#408C40"
          item.message = "出勤";
        }else{
          item.color="#D73636"
          item.message = "旷工 迟到";
        }
      })
    }).catch((err)=>{
      console.log(err)
    })
  },
};
</script>
<style lang="less" scoped>
.wrapper{
  margin-left:8%;
.container {
  width: 900px;
  margin: 0 auto;
  p {
    font-family: "宋体";
    font-weight: bolder;
    font-size: 20px;
    line-height: 40px;
  }
  .signin {
    margin-left: 50px;
    margin-top: 20px;
    padding-bottom: 10px;
    td {
      width: 100px;
      height: 30px;
      text-align: center;
      color: darkslateblue;
      font-family: "宋体";
      button {
        width: 100px;
        height: 30px;
        background-color: #3B4966;
        border: none;
        color: #ffffff;
      }
    }
  }
  hr {
    width: 100%;
    height: 1px;
    border: none;
    background-color: #cccccc;
  }
  .historySignIn {
    margin-top: 20px;
    table {
      width: 700px;
      margin:0 auto;
      margin-top:20px;
      border: 1px solid #cccccc;
      font-family: "宋体";
      border-collapse: collapse;
      text-align: center;
      tr:nth-child(even){
        background-color: #F9F9F9;
      }
      th,td {
        width: 150px;
        height: 30px;
        line-height: 30px;
      }
    }
    .btn{
      width: 700px;
      height:30px;
      margin:0 auto;
      display: flex;
      justify-content:space-around;
      a{
        border:none;
        background:none;
        font-family: "仿宋";
        margin-top: 20px;
        color:inherit;
        text-decoration: none;
      }
      .page{
        font-family: "仿宋";
        margin-top: 20px;
      }
    }
  }
}
}
</style>



