<template>
  <div class="wrapper">
    <img class="background" src="../assets/images/background.jpg">
    <div class="container">
      <p class="title">{{title}}</p>
      <form v-show="isShow[0]" target="targetIfr">
          <p>
            用户名：<input type="text" name="loginName" id="loginName" placeholder="用户名/邮箱/手机号">
            <a href="javascript:void(0)" @click="changeForm(1)">
              注册账号
            </a>
          </p>
          <br>
          <p>
            密&emsp;码：<input type="password" name="password" id="password">
            <a href="javascript:void(0)" @click="changeForm(2)">
              找回密码
            </a>
          </p>
          <br>
          <p><button class="submit" @click="Path">登&emsp;录</button></p> 
      </form>
      <form  v-show="isShow[1]" target="targetIfr">
          <p>
            姓&emsp;名：&emsp;<input type="text" name="userName" id="userName">
          </p>
          <p>
            性&emsp;别：&emsp;&emsp;&emsp;<input type="radio" name="userSex"  value="M">男
                        &emsp;&emsp;&emsp;<input type="radio" name="userSex"  value="W">女
          </p>
          <p>
            婚&emsp;姻：&emsp;&emsp;&emsp;<input type="radio" name="userMarriage" value="1">已婚
                        &emsp;&emsp;<input type="radio" name="userMarriage" value="0">未婚
          </p>
          <p>
            部&emsp;门：&emsp;
            <select name="userDepartment" id="userDepartment">
              <option>人力资源部</option>
              <option>财务部</option>
              <option>生产技术部</option>
              <option>营销部</option>
              <option>行政部</option>
            </select>
          </p>
          <p>
            电&emsp;话：&emsp;<input type="text" id="userPhone" name="userPhone">
          </p>
          <p>
            邮&emsp;箱：&emsp;<input type="text" id="userMile" name="userMile">
          </p>
          <p>
            密&emsp;码：&emsp;<input type="password" id="userPassword" name="userPassword">
          </p>
          <p>
            确认密码：<input id="userRepassword" type="password">
          </p>
          <p>
           验证码：&emsp;<input id="textNumber" type="text" class="textNumber">
           <button @click="getTextNumber">获取验证码</button>
          </p>
          <br>
          <p><button class="submit" @click="rigester">注&emsp;册</button></p> 
          <a href="javascript:void(0)" @click="changeForm(0)">
            返回登录
          </a>
      </form>
      <form v-show="isShow[2]" target="targetIfr">
        <p>
          邮&emsp;箱：&emsp;<input type="text" name="mail" id="mail" placeholder="请输入您的邮箱">
        </p>
        <p>
          验证码：&emsp;<input type="text" class="textNumber" id="passwordNumber">
          <button @click="getPasswordNumber">获取验证码</button>
        </p>
        <br>
        <p><button class="submit" @click="ifPasswordNumber">确&emsp;定</button></p> 
        <a href="javascript:void(0)" @click="changeForm(0)">
            返回登录
        </a>
      </form>
          <form v-show="isShow[3]" target="targetIfr">
        <p>
          密&emsp;码&emsp;&emsp;<input type="text" id="newPassword" placeholder="重新设置密码">
        </p>
        <p>
          确认密码：<input type="text" id="newRePassword">
        </p>
        <p><button class="submit" @click="changePassword">确&emsp;定</button></p> 
        <a href="javascript:void(0)" @click="changeForm(0)">
            返回登录
        </a>
      </form>
    </div>
      <iframe name="targetIfr" style="display:none"></iframe> 
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
var theTitle = ["用户登录", "用户注册", "找回密码"];
var userInformation={};
var userMail;
var rigesterNumber;
var getPasswordNumber;
import axios from 'axios';
import theAjax from "@/util/ajax";
export default {
  name: "enter",
  data() {
    return {
      title: theTitle[0],
      isShow: [true, false, false, false]
    };
  },
  methods: {
    changeForm: function(n) {
      this.isShow = [false, false, false,false];
      this.isShow.splice(n, 1, !this.isShow[n]);
      this.title = theTitle[n];
    },
    Path: function() {
      var _this = this;
      var loginName = document.getElementById("loginName").value;
      var password = document.getElementById("password").value;
      var obj = {
        method: "get",
        url:
          urlHeader+"/user/userLogin?loginName=" +
          loginName +
          "&password=" +
          password
      };
      var xhr = new XMLHttpRequest();
      xhr.open("get",urlHeader+"/user/userLogin?loginName="+loginName+"&password="+password,true);
      xhr.send(null);
      xhr.onreadystatechange = function(){
          if(xhr.readyState == 4 ){
              if((xhr.status>=200&&xhr.status<300)||xhr.status==304){
                  userInformation=JSON.parse(xhr.responseText);
                  //console.log(userInformation);
                  if(userInformation.code!=1)
                  {
                    alert("登陆失败");
                  };
                  if(userInformation.code){
                    localStorage.setItem("information",xhr.responseText);
                   // console.log(userInformation.data.userState);
                    _this.$router.push("/user");
                 }
              }
          }
      } 
    },
    getTextNumber:function(){
      userMail = document.getElementById("userMile").value;
      axios({
        method:"post",
        url:urlHeader+"/mail/getCode?mail="+userMail
      }).then((Response)=>{
        rigesterNumber = Response.data.data;
        if(rigesterNumber){
          alert("验证码已发送至邮箱！");
        }else{
          alert("出现错误，请稍后再试！");
        }
      }).catch((err)=>{
        console.log(err);
      })
    },
    rigester:function(){
      var sex,marriage;
      var userName = document.getElementById("userName").value;
      var userDepartment = document.getElementById("userDepartment").value;
      // console.log(userDepartment);
      // var department = userDepartment.options[selected].value;
      var userSex = document.getElementsByName("userSex");
      var userMarriage = document.getElementsByName("userMarriage");
      var userPhone = document.getElementById("userPhone").value;
      var userPassword = document.getElementById("userPassword").value;
      var userRepassword = document.getElementById("userRepassword").value;
      var textNumber = document.getElementById("textNumber").value;
      for(var i=0;i<2;i++){
        if(userSex[i].checked){
          sex = userSex[i].value;
        } 
      }
      for(var i=0;i<2;i++){
        if(userMarriage[i].checked){
          marriage = userMarriage[i].value;
        }
      }
      if(userRepassword!=userPassword){
        alert("两次密码不一致！");
      }else if(textNumber!=rigesterNumber){
        alert("验证码错误！");
      }else{
        axios({
          method:"post",
          url:urlHeader+"/user/userRegister?userName="+
          userName+
          "&userDepartment="+
          userDepartment+
          "&userSex="+
          sex+
          "&userMarriage="+
          marriage+
          "&userPassWord="+
          userPassword+
          "&userPhone="+
          userPhone+
          "&userMail="+
          userMail
        }).then((Response)=>{
          if(Response.data.code==1){
            alert("注册成功");
          }else{
            alert("注册失败");
          }
        }).catch((err)=>{
          console.log(err);
        })
      }
    },
    getPasswordNumber:function(){
      var that=this;
      userMail = document.getElementById("mail").value;
      axios({
        method:"post",
        url:urlHeader+"/mail/changePassword?mail="+userMail
      }).then((Response)=>{
        getPasswordNumber = Response.data;
        if(getPasswordNumber.data[1]){
          alert("验证码已发送至邮箱！");
        }else{
          alert("出现错误，请稍后再试！");
        }
      }).catch((err)=>{
        console.log(err);
      })
    },
    ifPasswordNumber:function(){
      var passwordNumber = document.getElementById("passwordNumber").value;
       if(getPasswordNumber.data[1]==passwordNumber){
        this.changeForm(3);
      }else{
        alert(验证码错误);
      }
    },
    changePassword:function(){
      var newPassword = document.getElementById("newPassword").value;
      var newRePassword = document.getElementById("newRePassword").value;
      if(newPassword!=newRePassword){
        console.log("两次密码不一致!");
      }else{
        axios({
          method:"post",
          url:urlHeader+"/user/userUpdate?userId="+getPasswordNumber.data[0].userId+
          "&userDepartment="+
          getPasswordNumber.data[0].userDepartment+
          "&userName="+
          getPasswordNumber.data[0].userName+
          "&userMail="+
          getPasswordNumber.data[0].userMail+
          "&userPassWord="+
          newPassword+
          "&userSex="+
          getPasswordNumber.data[0].userSex+
          "&userPhone="+
          getPasswordNumber.data[0].userPhone
        }).then((Response)=>{
          if(Response.data.code){
            alert("修改成功");
          }else{
            alert("修改失败");
          }
        }).catch((Err)=>{
          console.log(Err);
        })
      }
    }
  },
  mounted:function(){
    localStorage.clear();
  }
};
</script>


<style lang="less" scoped>
.wrapper {
  width: 100%;
  height: 100%;
  a {
    color: inhert;
  }
  .background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }
  .container {
    position: relative;
    top: 10;
    width: 400px;
    height: auto;
    background: rgba(255, 255, 255, 0.8);
    margin: 0 auto;
    margin-top: 4%;
    padding-bottom: 30px;
    border-radius: 10px;
    box-shadow: 4px 4px 10px #cccccc;
    .title {
      font-size: 18px;
      font-family: "幼圆";
      font-weight: bolder;
      display: block;
      width: 400px;
      height: 40px;
      background-color: #3b4966;
      color: #ffffff;
      text-align: center;
      line-height: 40px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }
    form {
      font-family: "幼圆";
      padding-top: 20px;
      p {
        margin-left: 30px;
        margin-top: 10px;
        font-size: 13px;
        a {
          color: #205aa4;
          font-size: 12px;
          font-family: "宋体";
          font-weight: normal;
          margin-left: 5px;
          line-height: 30px;
        }
      }
      input[type="password"],
      input[type="text"] {
        width: 220px;
        height: 25px;
        padding-left: 3px;
        border: 1px solid #cccccc;
        border-radius: 5px;
        font-family: "仿宋";
        color: black;
      }
      input[class="textNumber"] {
        width: 100px;
      }
      button {
        background-color: #3b4966;
        color: #ffffff;
        padding-right: 20px;
        padding-left: 20px;
        letter-spacing: 2px;
        height: 30px;
        border-radius: 3px; // outline: none;
        border: none;
      }
      button[class="submit"] {
        margin-left: 120px;
      }
      a {
        color: #000000;
        font-size: 12px;
        font-family: "宋体";
        font-weight: bolder;
        float: right;
        margin-right: 10px;
      }
    }
  }
}
</style>



