<template>
  <div class="wrapper">
    <ul class="nav">
      <div class="someHeight"></div>
      <router-link to="/user" tag="li">
        <img src="../assets/images/user.png">
          个人中心
      </router-link>
      <div>
      <router-link to="/signIn" tag="li">
          <img src="../assets/images/signIn.png">
          打卡
      </router-link>
      <a href="javascript:void(0)">
        <li @click="showChild(0)">
          <img src="../assets/images/holiday.png">
          假勤申请&emsp;&emsp;
          <span>></span>
        </li>
      </a>
      </div>
        <ul v-show="isShow[0]">
          <router-link to="/leave" tag="li">
              请假申请
          </router-link>
          <router-link to="/businessTrip" tag="li">
              出差申请
          </router-link>
          <router-link to="/leaveHistory" tag="li">
              申请进度
          </router-link>
        </ul>
      <a href="javascript:void(0)">
        <li @click="showChild(1)" v-show="routerChange[0]">
          <img src="../assets/images/form.png">
          查看相应情况
          <span>></span>
        </li>
      </a>
        <ul v-show="isShow[1]">
          <router-link to="/seeAll" tag="li">
              打卡情况
          </router-link>
          <router-link to="/seeLeave" tag="li">
              请假情况
          </router-link>
          <router-link to="/seeBusinessTrip" tag="li">
              出差情况
          </router-link>
        </ul>
      <div v-show="routerChange[1]">
      <router-link to="/leaveCheck" tag="li">
        <img src="../assets/images/attachment.png">
          请假审核
      </router-link>
      <router-link to="/businessTripCheck" tag="li">
        <img src="../assets/images/business.png">
          出差审核
      </router-link>
      </div>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'usercommon',
  props:['message'],
  data(){
    return{
      isShow:[false,false],
      routerChange:[false,false]
    }
  },
  methods:{
    showChild:function(n){
      this.isShow.splice(n, 1, !this.isShow[n])
    }
  },
  watch:{
    message:function(newVal,oldVal){
      if(newVal==0){
        this.routerChange.splice(0,2,false,false);
      }else if(newVal==1){
        this.routerChange.splice(0,2,true,true);
        console.log(this.routerChange);
      }else{
        this.routerChange.splice(0,2,true,false);
      }
    }
  },
  mounted:function(){
    var userState = JSON.parse(localStorage.getItem("information")).data.userState;
      if(userState==0){
        this.routerChange.splice(0,2,false,false);
      }else if(userState==1){
        this.routerChange.splice(0,2,true,true);
        console.log(this.routerChange);
      }else{
        this.routerChange.splice(0,2,true,false);
      }
  }
}
</script>

<style lang="less" scoped>
.wrapper{
    position: absolute;
    width: 15%;
    height: 92%;
  a{
    color:inherit;
    text-decoration: none;
  }
  ul{
    display: block;
    width: 100%;
    height: 100%;
    background-color:#3B4966;
    .someHeight{
      height:30px;
    }
    li{
      color:#C0C6D3;
      word-spacing: 2px;
      height:40px;
      font-size: 14px;
      line-height: 40px; 
      img{
        width: 18px;
        height: 18px;
        vertical-align: middle;
        padding:0px 5px 0px 10px;
      }
      span{
       padding-left: 30px;
      }
    }
    li:hover{
      background-color: #205AA4;
      color:#ffffff;
    }
    ul{
      padding-top:0px;
      width: 100%;
      height: 120px;
      background-color: #2C374D;
      li{
        height:40px;
        line-height: 40px;
        padding-left: 40px;
      }
    }
  }
}

</style>



