import Vue from 'vue'
import Router from 'vue-router'
import enter from '@/components/enter'
import user from '@/components/user'
import signIn from '@/components/signIn'
import leave from '@/components/leave'
import businessTrip from '@/components/businessTrip'
import leaveHistory from '@/components/leaveHistory'
import seeAll from '@/components/seeAll'
import seeLeave from '@/components/seeLeave'
import seeBusinessTrip from '@/components/seeBusinessTrip'
import leaveCheck from '@/components/leaveCheck'
import businessTripCheck from '@/components/businessTripCheck'
Vue.use(Router)

var router = new Router({
  mode: 'hash',
  routes: [
    {
      path:'/enter',
      name:'enter',
      component: enter
    },
    {
      path: '/user',
      name: 'user',
      component: user,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/signIn',
      name: 'signIn',
      component: signIn,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/leave',
      name: 'leave',
      component: leave,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/businessTrip',
      name: 'businessTrip',
      component: businessTrip,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/leaveHistory',
      name: 'leaveHistory',
      component: leaveHistory,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/seeAll',
      name: 'seeAll',
      component: seeAll,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/seeLeave',
      name: 'seeLeave',
      component: seeLeave,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/seeBusinessTrip',
      name: 'seeBusinessTrip',
      component: seeBusinessTrip,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/leaveCheck',
      name: 'leaveCheck',
      component: leaveCheck,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path: '/businessTripCheck',
      name: 'businessTripCheck',
      component: businessTripCheck,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
    }  
  ]
})
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {  // 判断该路由是否需要登录权限
    var information = JSON.parse(localStorage.getItem("information"));
    if (information) {  // 通过vuex state获取当前的token是否存在
      next();
    }
    else {
      next({
        path: '/enter',
        query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
  }
  else {
    next();
  }
 });
 export default router;




