<template>
  <div class="wrapper">
        <table>
          <tr>
            <td>
              申请人：&emsp;<input type="text" name="name" :value="message.userName"> 
            </td>
            <td>
               员工编号：<input type="text" name="userId" :value="message.leave.userId">
            </td>
            <td>
               就职部门：<input type="text" name="department" :value="message.department">
            </td>
          </tr>
          <tr>
            <td>
                开始时间：<input id="startTime" type="text" :value="message.leave.start"/>
            </td>
            <td>
                结束时间：<input id="startTime" type="text" :value="message.leave.end"/>
            </td>
            <td>
               请假类型：<input type="text" v-if="message.leave.type" value="病假">
               <input type="text" v-else value="事假">
            </td>
          </tr>
          <tr>
            <td colspan="3">
              请假事由：
              <textarea name="moreReason" v-model="message.leave.reason"></textarea>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <input type="radio" name="warning" value="紧急" :checked="message.leave.isExigency">紧急事务，望上级尽快批准
            </td>
          </tr>
          <tr>
              <td>
                  <button @click="sendMessage(1)">同意</button>
              </td>
              <td>
                  <button @click="sendMessage(2)">驳回</button>
              </td>
              <td>
                  <button @click="sendMessage(0)">暂不处理</button>
              </td>
          </tr>
        </table>
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
import axios from 'axios'
export default {
  name:"checkInformation",
  props:['message'],
  methods:{
      sendMessage:function(n){
        this.$emit("listenChange",false);
        var auditorName = JSON.parse(localStorage.getItem("information")).data.userName;
        var auditorId = JSON.parse(localStorage.getItem("information")).data.userId;
        console.log(auditorName);
        console.log(this.message);
        axios({
          method:"post",
          url:urlHeader+"/leave/updateLeave?auditorName="+auditorName+
          "&auditorId="+auditorId+
          "&date="+this.message.leave.date+
          "&start="+this.message.leave.start+
          "&end="+this.message.leave.end+
          "&site="+this.message.leave.site+
          "&reason="+this.message.leave.reason+
          "&userId="+this.message.leave.userId+
          "&userName="+this.message.userName+
          "&status="+n+
          "&id="+this.message.leave.id+
          "&type="+this.message.leave.type
        }).then((Response)=>{
          if(Response.data.code==1){
            alert("操作成功");
          }else{
            alert("操作失败");
          }
        }).catch((err)=>{
          console.log(err);
        })
      }
  }
}
</script>
<style lang="less" scoped>
table {
  font-family: "仿宋";
  font-size: 13px;
  width: 700px;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  box-shadow: 3px 5px 2px #cccccc;
  text-align: center;
  input[type="text"] {
    width: 120px;
    height: 20px;
    padding-left: 5px;
  }
  td {
    width: 220px;
    height: 50px;
  }
  textarea {
    width: 570px;
    height: 175px;
    resize: none;
    font-size: 15px;
    padding: 5px;
  }
  button {
    font-family: "仿宋";
    font-size: 13px;
    background-color: #3b4966;
    color: #ffffff;
    padding: 5px 15px 5px 15px;
    letter-spacing: 2px;
    height: 30px;
    border-radius: 3px; // outline: none;
    border: none;
  }
}
</style>



