<template>
  <div class="wrapper">
    <div class="container">
      <h1>
        申请进度
      </h1>
      <hr/>
      <div class="loading">{{loading}}</div>
        <table>
          <tr>
            <th>提交申请时间</th>
            <th>申请类别</th>
            <th>申请情况</th>
            <th>审核人</th>
          </tr>
          <tr v-for="(item,index) in data" :key="index" v-if="start<=index&&index<=end">
            <td>{{item.leave.date}}</td>
            <td>{{item.message}}</td>
            <td :style="{color:item.color}">{{item.information}}</td>
            <td>{{item.auditorName}}</td>
          </tr>
        </table>
        <div class="btn">
          <a href="javascript:void(0)" class="last" @click="lastPage">上一页</a>
          <span class="page">{{now}}/{{all}}</span>
          <a href="javascript:void(0)" class="next" @click="nextPage">下一页</a>
        </div>
    </div>
  </div>
</template>
<script>
var urlHeader="http://120.78.187.54:8080/text";
import axios from 'axios';
export default {
  name:"leaveHistory",
  data(){
    return {
      data:[],
      start:0,
      end:10,
      now:1,
      all:0,
      loading:"正在获取数据..."
    }
  },
  mounted:function(){
    var userId = JSON.parse(localStorage.getItem("information")).data.userId
    axios({
      method:"post",
      url:urlHeader+"/leave/getAllLeaveById?userId="+userId
    }).then((response)=>{
      this.data=response.data.data;
      this.all = Math.ceil(this.data.length/10);
      this.data.forEach((item,index,array)=>{
        if(item.leave.type==0){
          item.message ="事假";
        }else if(item.leave.type==1){
          item.message ="病假";
        }else{
          item.message = "出差";
        }
        if(item.leave.status==0){
          item.information="未处理";
          item.color="#FF39FF";
        }else if(item.leave.status==1){
          item.information="同意";
          item.color="#408C40"
        }else{
          item.information="驳回";
          item.color="#d73636";
        }
      })
      this.loading="";
    }).catch((err)=>{
      console.log(err);
    })
  },
  methods:{
    lastPage: function(){
      if(this.start<=0){
        alert("已经是第一页了");
      }else{
        this.start-=10;
        this.end-=10;
        this.now--;
      }
    },
    nextPage: function(){
      if(this.end>this.data.length){
        alert("已经是最后一页了");
      }else{
        this.start+=10;
        this.end+=10;
        this.now++;
      }
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper{
  margin-left:8%;
.container{
  width: 900px;
  height:100%;
  margin: 0 auto;
  h1{
    font-family: "宋体";
    font-size: 20px;
    line-height: 40px;
  }
  hr {
    width: 100%;
    height: 1px;
    border: none;
    background-color: #cccccc;
  }
  .loading{
      width:170px;
      margin:0 auto;
      margin-top: 5px;
  }
    table {
      width: 700px;
      margin:0 auto;
      margin-top: 50px;
      border: 1px solid #cccccc;
      font-family: "宋体";
      border-collapse: collapse;
      text-align: center;
      tr:nth-child(even){
        background-color: #F9F9F9;
      }
      th,td {
        width: 150px;
        height: 30px;
        line-height: 30px;
      }
    }
    .btn{
      width: 700px;
      height:30px;
      margin:0 auto;
      display: flex;
      justify-content:space-around;
      a{
        border:none;
        background:none;
        font-family: "仿宋";
        margin-top: 20px;
        color:inherit;
        text-decoration: none;
      }
       .page{
        font-family: "仿宋";
        margin-top: 20px;
      }      
    }
  }
}
</style>



